import React, {Fragment} from 'react';
import { Table } from 'reactstrap';

import PageTitle from '../../../Layout/AppMain/PageTitle';


export default class TableStriped extends React.Component {
  render() {
    return (
        <Fragment>
        <PageTitle
            heading="課程資料"
            // subheading="Choose between regular React Bootstrap tables or advanced dynamic ones."
            icon="pe-7s-medal icon-gradient bg-tempting-azure"
        />
    
      <Table striped className="mb-0">
        <thead>
          <tr>
            <th>課程名稱</th>
            <th>課程類別</th>
            <th>更新時間</th>
            <th>檔案文件</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <th scope="row">環境介紹</th>
            <td>新人基礎訓練</td>
            <td>2018.05.10</td>
            <td>PDF</td>
          </tr>
          <tr>
            <th scope="row">鞋面工程</th>
            <td>在職訓練課程</td>
            <td>2019.10.10</td>
            <td>PDF</td>
          </tr>
          <tr>
            <th scope="row">八大系統介紹</th>
            <td>專業訓練課程</td>
            <td>2020.01.15</td>
            <td>PDF</td>
          </tr>
        </tbody>
      </Table>
      </Fragment>
    );
  }
}
